              <el-form-item label="" prop="price">
                <el-input-number v-model="elform.price" controls-position="right" :min="0"></el-input-number>
              </el-form-item>
              <el-form-item label="">
                <div>
                  <vue-core-image-upload :crop="false" inputOfFile="imageFile" :url="upload" extensions="png,gif,jpeg,jpg" :class="['el-button', 'el-button--primary']" :max-file-size="5242880" :data="imageData" text="" :multiple="true" :multiple-size="30" credentials="true"
                    @imageuploaded="imageuploaded" @errorhandle="handleError">
                  </vue-core-image-upload>
                </div>
              </el-form-item>
            </el-form>
            <div style="border: 1px dashed #d9d9d9;width:100%;min-height:358px;">
              <el-row :gutter="20" style="margin-top:10px;">
                <el-col :span="6" v-for="imageUrl in imageUrls ">
                  <el-card :body-style="{ padding: '0px' }" class="mould-card">
                    <img class="image" v-bind:src="imageUrl" style="height:320px;">
                    <div style="text-align:center;">
                      <el-button type="text" @click="delMouldImage(imageUrl)"><i class="el-icon-error" style="color:red;"></i></el-button>
                    </div>
                  </el-card>
                </el-col>
              </el-row>
            </div>
            <div slot="footer" class="dialog-footer">
              <el-button type="primary" @click="toAddProduct"> </el-button>
              <el-button @click="innerVisible = false"> </el-button>
            </div>
          </el-dialog>
        </div> -->
      </el-main>
    </el-container>
  </div>
</div>
</template>
<script>
import VueCoreImageUpload from 'vue-core-image-upload';
import JLApiUtils from '../../utils/JLApiUtils.js';
export default {
  components: {
    VueCoreImageUpload
  },
  props: {
    title: String
  },
  data() {
    return {
      urlId: '',
      table_loading: false,
      imageUrls: [],
      ruleForm: {
        tax: '17',
        addUserId: '',
        addUserName: ''
      },
      elform: {
        productNum: '0',
        area: '0',
        dongMo: false,
        price: '0',
        coefficient: ''
      },
      productData: [],
      innerVisible: false,
      tableData: [],
      select_word: '',
      nowTab: 1,
    }
  },
  methods: {
    splitUrl(item) {
      let picurl = item.picUrls;
      let urlArray = [];
      if (picurl != "undefined" && picurl != "") {
        urlArray = picurl.split(',');
      }
      return urlArray;
    },
    toAudit(item) {
      let params = {
        id:this.urlId,
        audit:item,
        addUserId:this.ruleForm.addUserId,
        auditId:this.uid,
        auditName: this.name
      }
      this.postRequest("/audit/per/check", params).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.$message.success("");
          this.$store.state.stomp.send("/ws/nf", {}, '');
          this.$router.push('/audit/per')
        } else {
          this.$message.error("");
        }
      })
    },
    getInfo(id) {
      let self = this;
      this.getRequest("/audit/per/info?id=" + id).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.ruleForm = resp.data.data.yuinfo
          let product = resp.data.data.yuinfo.yuProductList
          if ('undefined' != self.product && product.length > 0) {
            let productItem = [];
            for (var i = 0; i < product.length; i++) {
              let picUrls = [];
              let item = {}
              item = product[i]
              picUrls = self.splitUrl(product[i])
              item.picUrls = picUrls;
              productItem.push(item);
            }
            self.productData = productItem
          }

        } else {
          self.$message.error("");
        }
      })
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  mounted: function() {
    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');
  },
  created: function() {
    this.urlId = this.$route.params.id;
    this.getInfo(this.urlId);
  }
}
</script>



